import { Col, Row, Layout, Typography } from "antd";
import Link from "next/link";
import React from "react";
import { useSelector } from "react-redux";

const { Footer } = Layout;
const { Text } = Typography;
function CFooter() {
  const date = new Date();
  const year = date.getFullYear();

  const { config } = useSelector((state) => state.config);
  return (
    <Footer
      style={{
        backgroundColor: "#fdfdfe",
        paddingTop: 10,
        paddingBottom: 10,
      }}
    >
      <Row>
        <Col
          span={12}
          style={{
            textAlign: "left",
          }}
        >
          <Text strong>{config.app_name}</Text>
        </Col>
        <Col
          span={12}
          style={{
            textAlign: "right",
          }}
        >
          <Text strong>
            Copyright &copy; 2021-{year} &bull;
            <Link href="/" style={{ color: "#0071EB" }}>
              {" " + config.app_title}
            </Link>
          </Text>
        </Col>
      </Row>
    </Footer>
  );
}

export default CFooter;
